

.sidebar{
    border: 1px solid #BFC4CD;
}

.sidebarHead{
    border-bottom: 1px solid #BFC4CD;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebarContent{
    ul{
        list-style: none;
        margin: 0;
        padding: 0;

        li{
            padding-left: 25%;
            border-bottom: 1px solid #BFC4CD;
            display: block;
            display: flex;
            justify-content: flex-start;

            img{
                width: 20px;
                height: 15px;
            }
        }
    }


}