
.exchange{
   
    min-height: 16vw;
    border: 1px solid #BFC4CD;
    padding: 1vw 0 1vw 0;
}



.exchangeContainer{
    width: 90%;
    margin: 0 auto;
}

.exchangeHead{
    font-weight: bold;
    padding: 1vw;
}

.exchangeContent{


    ul{
        list-style: none;
        margin: 0;
        padding: 0;

        li{
            padding-left: 25%;
            border-bottom: 1px solid #BFC4CD;
            display: block;
            margin: .5vw auto .5vw auto;
            display: grid;
            grid-template-columns: 1fr 1fr;
            

            img{
                width: 20px;
                height: 15px;
            }
        }
    }
}