@import url(https://fonts.googleapis.com/css2?family=Lato:wght@900&family=Roboto:ital,wght@1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@900&family=Roboto:ital,wght@1,500&display=swap);

#root{color:#000980}

.exchange{min-height:16vw;border:1px solid #BFC4CD;padding:1vw 0 1vw 0}.exchangeContainer{width:90%;margin:0 auto}.exchangeHead{font-weight:bold;padding:1vw}.exchangeContent ul{list-style:none;margin:0;padding:0}.exchangeContent ul li{padding-left:25%;border-bottom:1px solid #BFC4CD;display:block;margin:.5vw auto .5vw auto;display:grid;grid-template-columns:1fr 1fr}.exchangeContent ul li img{width:20px;height:15px}


nav{text-align:center;min-height:5vw}nav ul{list-style:none;margin:0;padding:0;display:block;min-height:5vw;display:grid;grid-template-columns:repeat(4, 1fr)}nav ul li{display:inline-block;align-self:center;transition:all .2s ease-in}nav ul li a{text-decoration:none;font-family:'Roboto', sans-serif;font-size:1.5vw;color:#223046}nav ul li a:hover{color:black}

.header{min-height:5vw}.header hr{border:1px solid #223046}.headerWrap{display:grid;grid-template-columns:1fr 3fr 1fr}.logo{display:flex;justify-content:center;align-items:center}.logo a{text-decoration:none}.logo h2{font-family:'Lato', sans-serif;color:#223046;font-size:1.6vw;cursor:pointer}.person{display:flex;justify-content:center;align-items:center}.person i{color:#223046;font-size:1.6vw;cursor:pointer}

.sidebar{border:1px solid #BFC4CD}.sidebarHead{border-bottom:1px solid #BFC4CD;display:flex;justify-content:center;align-items:center}.sidebarContent ul{list-style:none;margin:0;padding:0}.sidebarContent ul li{padding-left:25%;border-bottom:1px solid #BFC4CD;display:block;display:flex;justify-content:flex-start}.sidebarContent ul li img{width:20px;height:15px}

.layout{max-width:1300px;margin:0 auto;padding:0}.content{margin-top:4vw;padding:0 0.5vw 0 0.5vw;display:grid;grid-template-columns:2.8fr 1.2fr;grid-column-gap:0.5vw}

.calculator{min-height:16vw;border:1px solid #BFC4CD}.calcContainer{width:90%;margin:0 auto;padding:1vw 0 1vw 0}

.btn{font-family:'Roboto', sans-serif;font-size:2vw;color:#223046;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;border:1px solid #BFC4CD;cursor:pointer;max-height:2.5vw}.btn:hover{color:#fff;background:#223046}

.operation{display:grid;grid-template-columns:2fr 1fr .5fr;grid-column-gap:1vw}

.calcResult{margin-top:2vw}.calcResult ul{list-style:none;margin:0;padding:0}.calcResult ul li{padding-left:25%;border-bottom:1px solid #BFC4CD;display:block;display:flex;justify-content:flex-start}.calcResult ul li img{width:20px;height:15px}

.sample{min-height:16vw;border:1px solid #BFC4CD}.sampleContainer{width:90%;margin:0 auto;padding:1vw 0 1vw 0}.sampleHead{width:70%;display:grid;grid-template-columns:2fr 1fr}.sampleResult{margin-top:2vw;margin-bottom:2vw}.sampleResult ul{list-style:none;margin:0;padding:0}.sampleResult ul li{padding-left:25%;border:1px solid #BFC4CD;display:block;margin-bottom:1vw;padding:.5vw;display:grid;grid-template-columns:2fr 2fr 2fr .3fr}.sampleResult ul li img{width:20px;height:15px}.sampleResult ul li button{padding:.1vw}

.info{min-height:16vw;border:1px solid #BFC4CD;padding:2vw}

.dark{position:fixed;top:0;bottom:0;left:0;right:0;opacity:.6;background-color:#1e1e1e;z-index:7;display:none}.showDark{display:block}

.modalBtn{margin:3vw auto;display:flex;justify-content:center;align-items:center}

.modalInput{margin:1vw auto;padding:.5vw;display:grid;grid-template-columns:1fr}.modalInput span{color:#223046;font-family:'Lato', sans-serif;font-size:.7vw;line-height:1.5vw}.modalInput input{height:1.3vw}.modalBtn{margin:3vw auto;display:flex;justify-content:center;align-items:center}

.modal{width:18.5vw;min-width:190px;height:25vw;min-height:200px;background:#B9D6F3;border:1px solid #ccc;opacity:1;z-index:9;position:absolute;margin:auto;top:0;left:0;right:0;bottom:0;border-radius:5px;display:none}.modalHead{padding:.7vw;display:flex;justify-content:space-between;align-items:center}.modalHead ul{margin:0;padding:0;list-style:none}.modalHead ul :nth-child(1){margin-right:.5vw}.modalHead ul li{display:inline-block}.modalHead ul li a{text-decoration:none;color:#e21eb1;font-family:'Roboto', sans-serif;font-size:1vw}.modalHead i{font-size:1vw}.modalShow{display:block}

.modalInput{margin:1vw auto;padding:.5vw;display:grid;grid-template-columns:1fr}.modalInput label{color:#223046;font-family:'Lato', sans-serif;font-size:.7vw;line-height:1.5vw}.modalInput input{height:1.3vw}.modalInput span{color:red;font-size:14px;font-weight:bold}.inputInvalid label{color:red}

